.notifications {
  margin-top: 20px;
}

.notification {
  background: $nav-back;
  padding: 20px 10px;
  margin-bottom: 15px;
  @extend %Metrophobic-font;
  border-radius: 10px;
  &.unseen {
    background: #032028;
  }
  .notification-content {
    .text-content {
      text-align: center;
      .user {

      }
      .username {
          @extend %Poppins-font;
          font-weight: bold;
          color:$main;
          font-size: 12px;
          text-decoration: none;
          margin-right: 10px;

          img {
            width: 35px;
            height: 35px;
            border-radius: 50%;
            margin-right: 5px;
          }
          i {
              margin-right: 5px;
              @include m576 {
                margin-right: 2px;
              }
          }
      }
      .time {
          font-size: 12px;
          position: relative;
          padding: 0 10px;
          color: $gray;
          @extend %font-family;
          @include m576 {
            font-size: 10px;
          }
          &:before {
              content: '';
              position: absolute;
              left:0;
              top: 50%;
              width: 3px;
              height: 3px;
              border-radius: 50%;
              background: $gray;
          }
      }
      .message-user {
        .msg {
          font-size: 14px;
          word-break: break-all;
          font-weight: bold;
          display: inline-block;
          margin: 0;
          padding-right: 5px;
          color: $gray;
          text-decoration: none;
          @include m576 {
            font-size: 12px;
          }
          .question {
            font-weight: 500;
            position: relative;
            color: $white;
            &:before,&:after {
              content: '"';
              padding:0 4px;
              font-weight: 700;
              color: $main;
            }
          }

        }
      }
    }
  }
}
