.settings {
    background: $nav-back;
    position: relative;
    border-radius: 5px;
    height: auto;
    overflow: hidden;
    padding: 10px 15px;
    @extend %Poppins-font;
    .main-column {
        section {
            padding: 15px 0;
            header {
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -ms-flex-pack: distribute;
                justify-content: space-around;
                background: $dark-page;
                border-radius: 5px;
                padding: 5px;

                .title-action {
                    -ms-flex-preferred-size: 0;
                    flex-basis: 0;
                    -webkit-box-flex: 1;
                    -ms-flex-positive: 1;
                    flex-grow: 1;
                    text-align: center;
                    cursor: pointer;
                    color: $gray;
                    font-weight: bold;
                    font-size: 12px;
                    padding: 5px 0;
                    &.active {
                        background: $white;
                        color: $dark-page;
                    }
                }
            }
            .message-true,.message-false {
                @extend %Poppins-font;
                padding: 8px 0;
                text-align: center;
                margin-bottom: 5px;
                color: #00cc00;
                font-size: 12px;
                font-weight: 500;
                border-radius: 5px;
            }
            .message-false {
                color: #d10000;
            }
            /*End Header*/
            .profile-user {
              display: none;
              .delete-image {
                text-align: center;
                text-decoration: none;
              }
            }
            .account-user {
              .delete-acc {
                text-align: center;
                text-decoration: none;
              }
            }

        }
    }
}
.pop-up-setting {
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba($dark-page,.9);
    z-index: 999;
    @extend %Poppins-font;
    .exit {
        color: $white;
        font-size: 18px;
        position: absolute;
        right: 15px;
        top: 5px;
    }
    .edit-user{
        @include itemCenter();
        text-align: left;
        width: 400px;
        height: auto;
        background: $nav-back;
        padding: 30px;
        border-radius:5px;
        display: none;
        @include m576 {
            width: 80%;
        }
        .header-title {
            @extend %Poppins-font;
            background: $main;
            color:$white;
            padding: 15px 0;
            margin-bottom: 20px;
            margin-top: 10px;
            position: relative;
            width: 100%;
            display: block;
            text-align: center;
            border-radius: 5px;
            span {
                font-size: 14px;
                font-weight: bold;
            }


        }
        label {
            font-size: 12px;
            font-weight: bold;
        }
        .form-control {
            background: none;
            margin-bottom: 20px;
            color: $gray;
            font-size: 12px;
            box-shadow: none;
            border-radius: 2px;
            border:1px solid #535353;
        }
    }
    .pop-photo {
      width: 600px;
      height: 200px;
      overflow: hidden;
      border-radius: 10px;
      padding: 20px 0;
      text-align: center;
      @include m576 {
        width: 80%;
      }
      .box-photo {
        height: 100%;
        img {
            width: 40px;
            height: 40px;
            display: block;
            margin: 0 auto;
            border-radius: 50%;
        }
        form {
            span {
                position: relative;
                display: block;
                text-align: center;
                input[type="file"] {
                  opacity: 0;
                  position: absolute;
                  top: 0;
                  left: 0;
                  width: 100%;
                  height: 100%;
                  cursor: pointer;
                }
            }
            .btn-submit {
              outline: inherit;
            }
        }
      }
    }
}
