.home-page {
    width: 100%;
    height: 100%;
    margin-top: 20px;
    .searched {
        position: relative;
        i {
            position: absolute;
            left: 0;
            top: 50%;
            color: #ff8f1f;
            transform: translate(50%,-50%);
            cursor: pointer;
        }
        .form-control {
            padding-left: 30px;
            font-size: 14px;
        }
    }
}
.unpost {
  text-align: center;
  color: $gray;
  margin-bottom: 20px;
  font-size: 12px;
  text-transform: uppercase;
  p {
    margin-bottom: 20px;
  }
  a {
    color: $gray;
    text-decoration: none;
    padding: 10px 20px;
    background: $nav-back;
    border-radius: 5px;
    font-weight: bold;
    @extend %Poppins-font;
    font-size: 12px;
    text-transform: uppercase;
  }
}
.post {
        padding: 10px 10px;
        position: relative;
        border-radius: 8px;
        margin-bottom: 15px;
        .post-content {
            .text-content {
                .question {
                    padding: 5px 10px;
                    position: relative;
                    height: auto;
                    .vertical-bar {
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 4px;
                        height: 100%;
                        border-radius: 5px;
                    }
                    .username {
                        @extend %Poppins-font;
                        font-weight: bold;
                        margin:0 5px;
                        font-size: 12px;
                        text-decoration: none;
                        img {
                          width: 35px;
                          height: 35px;
                          margin-right: 10px;
                          border-radius: 50%;
                        }
                        i {
                            margin-right: 5px;
                        }
                    }
                    .time {
                        font-size: 12px;
                        position: relative;
                        padding: 0 10px;
                        @include m576 {
                          font-size: 10px;
                        }
                        &:before {
                            content: '';
                            position: absolute;
                            left:0;
                            top: 50%;
                            width: 3px;
                            height: 3px;
                            border-radius: 50%;
                        }
                    }
                    .message {
                        @extend %arabic-font;
                        padding: 10px 20px;
                        font-weight: bold;
                        font-size: 16px;
                        text-align: start;
                        unicode-bidi: plaintext;
                        @include m576 {
                          font-size: 12px;
                        }
                    }
                }
                .reply {
                    padding: 5px 10px;
                    position: relative;
                    height: auto;
                    img {
                      width: 35px;
                      height: 35px;
                      margin-right: 15px;
                      border-radius: 50%;
                    }
                    .username {
                      @extend %Poppins-font;
                      font-weight: bold;
                      margin:0 5px;
                      font-size: 12px;
                      text-decoration: none;
                      img {
                        width: 35px;
                        height: 35px;
                        margin-right: 15px;
                        border-radius: 50%;
                      }
                    }
                    .action {
                        font-size: 10px;
                    }
                    .reply-text {
                        text-align: start;
                        unicode-bidi: plaintext;
                        @extend %arabic-font;
                        font-weight: bold;
                        font-size: 16px;
                        padding: 10px 20px;
                        overflow-wrap:break-word;
                        @include m576 {
                          font-size: 12px;
                          padding: 5px 10px;
                        }
                        span {
                            white-space: pre-wrap;

                        }
                    }
                }
            }
        }
        .options {
            position: absolute;
            top: 0;
            right: 10px;
            ul {
              display: none;
              visibility: hidden;
              opacity: 0;
              @include trans(all,.3s,linear);
            }
            &.active {
              ul {
                display: block;
                visibility: visible;
                opacity: 1;
                height: auto;
                background: $dark-page;
                padding: 5px 10px;
                position: absolute;
                right: 0;
                width: 120px;
                li {
                  text-align: center;
                  a {
                    color: $main;
                    text-decoration: none;
                    font-size: 12px;
                  }
                }
              }
            }
        }
        .actions {
            display: flex;
            justify-content: space-around;
                & > div {
                -webkit-flex-basis: 0;
                flex-basis: 0;
                -webkit-flex-grow: 1;
                flex-grow: 1;
                text-align: center;
                cursor: pointer;
                @include trans(all,.3s,linear);
                    &.like-button {
                        .like-counter {
                            margin: 0 5px;
                        }
                    }
            }
        }
    }
