.profile-username {
  position: relative;
  height: 250px;
  border-radius: .5em;
  overflow: hidden;
  background: $dark-page;
  margin-top: 20px;
  margin-bottom: 40px;
  .header {
    position: relative;
    height: 100%;
    .cover {
      z-index: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      object-fit: cover;
    }
    .overlay {
      @include overlay($dark-page,.6);
      display: flex;
      justify-content: center;
      align-items: center;
    }
    @include m768 {
      background-position: center center;
    }
  }
  .user {
    text-align: center;
    z-index: 9;
    width: 100%;
    .profile-img {
      margin: auto;
      width: 100px;
      height: 100px;
      @include m576 {
        width: 70px;
        height: 70px;
      }
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
    }
    .username {
      font-size: 12px;
      font-weight: bold;
      margin-top: 5px;
      display: block;
      text-decoration: none;
      color: $gray;
      span {
        display: block;
        color: $gray;
        font-size: 20px;
        font-weight: 500;
      }
    }
    .answer {
      display: flex;
      justify-content: space-between;
      margin-top: 20px;
      color: $gray;
      @extend %Raleway-font;
      font-weight: bold;
      font-size: 14px;
      .reanswer,.following,.follwers {
        flex-grow: 1;
        flex-basis: 0;
        .title {
          color: $main;
        }
      }
      span {
        display: block;
      }
    }
  }
}
.postest {
  margin-top: 10px;
  .questions {
    .form-ask {
      @extend %Raleway-font;
      background: rgba($black,.2);
      padding: 10px;
      .title-user {
        text-align: center;
        margin-bottom:10px;
        span {
          font-weight: bold;
          font-size: 12px;
          color: $gray;
          &.title {
            position: relative;
            &:after {
              content: '@';
              color: $main;
              margin-left: 5px;
              font-size: 10px;
            }
          }
          &.username {
            color: $main;
          }
        }
      }
      form {
        .form-control {
          @extend %arabic-font;
          box-shadow: none;
          color: $black;
          font-size: 16px;
          font-weight: bold;
          min-height: 20px;
          max-height: 40px;
          unicode-bidi: plaintext;
          text-align: start;
          margin-bottom: 30px;
          resize: none;
          @include m576 {
            font-size: 12px;
          }
        }
        .submit-section {
          display: flex;
          justify-content: space-between;
          .checkit {
            input[type="checkbox"] {
              width: 0;
              height: 0;
              &:checked + label {
                &:before {
                  background: $main;
                }
                &:after {
                  left: 16px;
                }
              }
            }
            label {
              position: relative;
              padding-left: 40px;
              font-size: 12px;
              @include m576 {
                font-size: 10px;
              }
              cursor: pointer;
              span {
                font-weight: bold;
                color: $gray;
              }
              &:before {
                @include trans(all,.3s,ease-in-out);
                position: absolute;
                content: '';
                left: 0;
                top: 0;
                width: 30px;
                height: 15px;
                border-radius: 50px;
                background: $gray;
              }
              &:after {
                @include trans(all,.3s,ease-in-out);
                position: absolute;
                content: '';
                left: 2px;
                top: 2px;
                width: 11px;
                height: 11px;
                border-radius: 50px;
                background: $white;
              }
            }
          }
          .submit-action {
            .btn-submit {
              background: $nav-back;
              color:$gray;
              @include m576 {
                width: 80px;
                height: 30px;
                font-size: 10px;
              }
            }
          }
        }
      }
    }
  }
}
