.friends {
  margin-top: 20px;
  @extend %font-family;
  .search-box {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    height:40px;
    margin-bottom: 20px;
    .form-control {
      background: transparent;
      border:none;
      @include border-gradient(top left, #153e47,$nav-back, 1px);
      color: $gray;
      font-size: 12px;
      letter-spacing: 1px;
      height: 100%;
      @include m576 {
        font-size: 10px;
      }
    }
    .btn-submit {
      position: absolute;
      right: 0;
      outline: inherit;
      background: linear-gradient(to right, #153e47, $nav-back);
      border-top-left-radius: 50px;
      border-bottom-left-radius: 50px;
      color: $main;
      font-weight: 500;
      font-size: 12px;
      @include m576 {
        font-size: 10px;
      }
      i {
        margin-right: 5px;
      }
    }
  }
  .text-center {
    margin: 40px 0;
    h3 {
      font-size: 24px;
      color: $gray;
      text-transform: uppercase;
      position: relative;
      @include m576 {
        font-size: 16px;
      }
    }
  }
  .friend {
     display: flex;
     margin-bottom: 20px;
     .rel-friend{
      text-decoration: none;
      flex-grow: 1;
      flex-basis: 0;
      .friends-list {
        display: inline-flex;
        justify-content: flex-start;
        align-items: center;
        img {
          width: 60px;
          height: 60px;
          margin-right: 20px;
          @include m576 {
            width: 40px;
            height: 40px;
            margin-right: 10px;
          }
        }
        .information-friend {
          @extend %font-family;
          .name {
            font-size: 16px;
            color: $main;
            margin: 0;
            margin-bottom: 5px;
            font-weight: bold;
            letter-spacing: 1px;
            @include m576 {
              font-size: 12px;
            }
          }
          .username {
            font-size: 12px;
            color: #6a6a6a;
            font-weight: bold;
            letter-spacing: 1px;
            margin: 0;
            padding-left: 5px;
            @include m576 {
              font-size: 10px;
            }
          }

        }
      }
    }
    .ask-him {
      .profile-link {
        color: $gray;
        background: transparent;
        text-decoration: none;
        font-size: 14px;
        text-transform: capitalize;
        letter-spacing: 1px;
        padding: 10px 15px;
        border-radius: 50px;
        font-weight: bold;
        @include trans(all,.3s,ease-in-out);
        @include m576 {
          font-size: 10px;
        }
        &:hover {
          background: linear-gradient(to right, #153e47, $nav-back);
        }
        i {
          margin-left: 3px;
          font-size: 10px;
        }
      }
    }
    .follow-him {
      @extend %Metrophobic-font;
      width: 90px;
      height: 30px;
      text-align: center;
      line-height: 30px;
      border-radius: 50px;
      background: linear-gradient(to right, #153e47, $nav-back);
      overflow: hidden;
      @include m576 {
        width: 65px;
      }
      .follow {
        color: $gray;
        text-decoration: none;
        font-size: 12px;
        letter-spacing: 1px;
        font-weight: bold;
        width: 100%;
        height: 100%;
        display: block;
        @include m576 {
          font-size: 10px;
        }
        &:hover {
          background: linear-gradient(to right, #10333a, #03151a);
        }
        &.unfollow {
          background: linear-gradient(to right, #591515, #b20000);
          &:hover {
            background:linear-gradient(to right, #411010, #8f0000);
          }
        }
      }
    }
  }
}
