.inbox {
    margin-top: 20px;
    .post {
        .post-content {
            .text-content {
                .question {
                  .username {
                    text-decoration: none;
                  }
                    .message {
                        text-align: start;
                        unicode-bidi: plaintext;
                        padding: 15px 10px;
                        margin: 0;
                        word-wrap: break-word;
                    }
                }
            }
        }
    }
  .actions {
    .reply-button {
      padding: 10px 0;
        text-align: right;
        cursor: auto;
        .btn-answer {
            background: none;
            padding: 10px 15px;
            border-radius: 20px;
            font-weight: bold;
            font-size: 12px;
            color: $main;
            text-decoration: none;
            @include trans(all,.3s,linear);
            &:hover,&:active {
                background: $dark-page;
            }
            i {
                margin-left: 5px;
                font-size: 10px;
            }
        }
    }
  }
    .reply-quest {
        display: none;
        position: relative;
        &.reply-message {
            display: block;
        }
        textarea {
            height: 100px;
            resize: none;
            background: none;
            border: 1px solid #2c464d;
            color: $gray;
            font-size: 16px;
            letter-spacing: 1px;
            text-align: start;
            unicode-bidi: plaintext;
            @extend %arabic-font;
            font-weight: bold;
            margin-bottom: 20px;
            padding: 10px 20px;
            &:focus {
                box-shadow: none;
            }
        }
        .btn-submit {
            display: block;
            margin-left: auto;
            i {
                margin-left: 5px;
            }
        }
    }
}
