.btn-submit {
    font-size: 12px;
    text-transform: uppercase;
    background: $dark-page;
    border:none;
    color: $gray;
    font-weight: bold;
    text-align: center;
    border-radius: 3px;
    width:120px;
    height: 40px;
    outline: none;
}
.changed-btn {
    display: block;
    width: 100%;
    padding: 15px 0;
    background: none;
    border: none;
    color: $main;
    font-weight: bold;
    // border-bottom: 1px solid $dark-page;
    margin-bottom: 10px;
    font-size: 12px;
    outline: inherit !important;
    @include trans(all,.3s,linear);
        &:hover {
            color: rgba($main,.7);
        }
    }
